/* Max Height Css Class Start */
.tw-max-h {
    &-screen {
      max-height: 100vh;
    }
    &-400-px {
      max-height: 400px;
    }
    &-350-px {
      max-height: 350px;
    }
    &-460-px {
      max-height: 460px;
    }
    &-796-px {
      max-height: 796px;
    }
}
/* Max Height Css Class End */
